import {Component, Injector, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, Validators} from '@angular/forms';
import {BaseComponent} from '../../../../@calls/components/base/base.component';
import {NgForm} from '@angular/forms';

@Component({
    selector: 'app-add-company',
    templateUrl: './add-company.component.html',
    styleUrls: ['./add-company.component.scss']
})
export class AddCompanyComponent extends BaseComponent implements OnInit {
    form: any;
    isLoading = false;
    isEdit = false;
    data: any;
    @ViewChild('ngForm') mytemplateForm: NgForm;
    errors = [];

    constructor(injector: Injector) {
        super(injector);
        if (this.router.getCurrentNavigation().extras.state) {
            this.data = this.router.getCurrentNavigation().extras.state.data;
            this.isEdit = true;
        }
    }

    ngOnInit(): void {
        this.initComponent();
    }

    initComponent(): void {
        this.form = this.fb.group({
            email: ['', Validators.required],
            password: ['', Validators.required],
            username: ['', Validators.required],
            login_id: ['', Validators.required],
            type: ['admin', Validators.required],
            companyName: ['', Validators.required]
        });
        if (this.isEdit) {
            const j = {
                email: this.data.email,
                password: this.data.password,
                username: this.data.username,
                login_id: this.data.login_id,
                type: 'admin',
                companyName: this.data.companyName
            };
            // this.dep_id = data.id;
            this.form.setValue(j);
        }
    }

    register(form, ngForm) {
        console.log(form);
        this.errors = [];
        this.isLoading = true;
        if (this.isEdit) {
            form['id'] = this.data.id;
            form['user_id'] = this.data.user_id;
            this.authService.updateCompany(form).subscribe((res) => {
                console.log(res);
                if (res.result === 'success') {
                    this.isLoading = false;
                    /* ngForm.resetForm();
                    this.form.onReset(); */
                    this.mytemplateForm.reset();
                    this.utilService.showAddedSuccessfullyToast();
                    this.router.navigateByUrl('pages/companies').then();
                } else {
                    const error = res.error_description;
                    let key;
                    this.isLoading = false;
                    for (key in error) {
                        console.log('Key: ' + key);
                        console.log('Value: ' + error[key]);
                        console.log(typeof error[key]);
                        if (typeof error[key] === 'string') {
                            if (error[key] !== 'error') {
                                this.errors.push(error[key]);
                            }
                        } else if (typeof error[key] === 'object') {
                            if (error[0] === 'error') {
                                this.errors.push('LoginID already exist !');
                            } else {
                                this.errors.push(error[key][0]);
                            }
                        } else {
                            this.errors.push('LoginID already exist !');
                        }
                    }
                }
            }, (err) => {
                console.log(err);
                this.isLoading = false;
                this.utilService.showConnectionErrorToast();
            });
        } else {
            this.authService.register(form).subscribe((res) => {
                if (res.result === 'success') {
                    console.log(res);
                    this.isLoading = false;
                    /* ngForm.resetForm();
                    this.form.onReset(); */
                    this.mytemplateForm.reset();
                    this.utilService.showAddedSuccessfullyToast();
                    this.router.navigateByUrl('pages/companies');
                } else {
                    const error = res.error_description;
                    let key;
                    this.isLoading = false;
                    for (key in error) {
                        console.log('Key: ' + key);
                        console.log('Value: ' + error[key]);
                        console.log(typeof error[key]);
                        if (typeof error[key] === 'string') {
                            if (error[key] !== 'error') {
                                this.errors.push(error[key]);
                            }
                        } else if (typeof error[key] === 'object') {
                            if (error[0] === 'error') {
                                this.errors.push('LoginID already exist !');
                            } else {
                                this.errors.push(error[key][0]);
                            }
                        } else {
                            this.errors.push('LoginID already exist !');
                        }
                    }
                }
            }, (err) => {
                console.log(err);
                this.isLoading = false;
                this.utilService.showConnectionErrorToast();
            });
        }
    }
}
